import React from 'react';
import ParserHTML from '../components/particles/ParseHTML';
import { graphql } from 'gatsby';
import Image from 'gatsby-image';
import Layout from './layout';
import Meta from './meta';

export const query = graphql`
    query($slug: String!) {
        allDatoCmsPaquete(filter: { slug: { eq: $slug } }) {
            nodes {
                    titulo
                    contenido
                    seoMetaTags {
                        tags
                      }
                    imagen {
                    fluid {
                        ...GatsbyDatoCmsFluid
                    }
                }
            }
        }
    }
`

const PaqueteTemplate = ({data: { allDatoCmsPaquete: { nodesÂ } }}) => {

    const { contenido, imagen, seoMetaTags, titulo} = nodes[0];

    return ( 
        <Layout>
               <Meta
                title={seoMetaTags.tags[0].content}
                description={seoMetaTags.tags[3].attributes.content}
                image={seoMetaTags.tags[13].attributes.content}
            />
            <main className="w-2/3 mx-auto my-8 list-none">
                <h1 className="text-center m-8">{titulo}</h1>
                <p>{ParserHTML(contenido)} </p>
                <Image className="w-3/5 mx-auto"
                    fluid={imagen.fluid}
                />
            </main>
        </Layout>
     );
}
 
export default PaqueteTemplate;